body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    padding: 10px;
}

.nav ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.nav ul li {
    list-style: none;
}

.nav ul li a {
    padding: 0 5px;
    text-decoration: none;
    text-transform: uppercase;
}
/* global element css */
ul li {
    list-style: none;
}

a {
    text-decoration: none;
    color: blue;
}

a:hover {
    color: orange;
}

.home-content {
    text-align: center;
}

/*About Me Page*/
.aboutme {}

.aboutme-top {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 20px;
}

.aboutme-top .image {
    width: 50%;
    text-align: right;
}

.aboutme-top .info {
    width: 50%;
}
